.searchPage {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
    width: 100%;
}

.searchResultContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.result {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 1vw;
    padding: 0.3vh 0;
    border-bottom: 1px solid black;
    cursor: pointer;
    width: 100%;
}

.result:hover {
    background-color: #cccccc;
}

.resultMainRow {
    display: flex;
    justify-content: space-between;
}

.resultMainRow .resultSecondary {
    font-size: 0.8rem;
    color: #888888;
    text-align: right;
}

.resultSubRow .resultSub {
    font-size: 0.75rem;
    color: #888888;
}

.searchResults {
    display: flex;
    flex-direction: column;
    width: 95vw;
}

.searchLoading {
    text-align: center;
}

.searchResultsHeader {
    display: flex;
    justify-content: space-between;
    margin: 0 1vw;
}
