.row {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0.5vh 0;
}

.values {
    display: flex;
    align-content: center;
    justify-content: space-between;
    width: 50%;
}

.bars {
    display: flex;
    align-items: center;
    width: 100%;
}

.barDivider {
    height: 17px;
    width: 0.5%;
    background-color: #000000;
}

.bar {
    background-color: tomato;
    height: 100%;
}

.remainder {
    background-color: #cccccc;
    height: 80%;
}

.barContainer {
    display: flex;
    align-items: center;
    height: 15px;
    width: 50%;
}
